<template>
  <div class="p-group-create">
    <common-header>
      <span
        slot="left"
        class="m-send-btn"
        @click="goBack"></span>
      <template slot="right">
        <circle-loading v-if="loading" />
        <a
          v-else
          :class="{ disabled }"
          class="m-send-btn"
          @click.prevent="handleOk"></a>
      </template>
    </common-header>

    <form>

      <form-avatar-item
        v-model="avatar"
        label=""
        shape="square"
        type="blob" />

      <form-input-item
        v-model="form.name"
        label=""
        maxlength="20"
        placeholder=", 20"/>

      <form-select-item
        v-model="category.name"
        label=""
        placeholder=""
        @click="switchCate"/>

      <!--  -->
      <form-tags-item v-model="tags" />

      <!--  -->
      <form-location-item v-model="location"/>

      <form-input-item
        v-model="form.summary"
        type="textarea"
        label=""
        maxlength="255"
        warnlength="200"
        placeholder=""/>

      <hr>

      <form-switch-item v-model="allowFeed" label=""/>

      <form-switch-item v-model="privateMode" label="" />

      <template v-if="privateMode">
        <section class="c-form-item bb">
          <label class="check-label" @click="needPaid = true">
            <input :value="needPaid" type="checkbox" >
            <span class="checkmark"/>
            <span></span>
          </label>
          <label class="label-amount">
            <input
              v-model="form.money"
              type="text"
              placeholder=""
              maxlength="8">
            <span>{{ currencyUnit }}</span>
          </label>
        </section>

        <section class="c-form-item bb">
          <label class="check-label" @click="needPaid = false">
            <input :value="!needPaid" type="checkbox" >
            <span class="checkmark"/>
            <span></span>
          </label>
        </section>
      </template>

      <p class="footage"></p>

      <form-input-item
        v-model="form.notice"
        type="textarea"
        label=""
        placeholder=""
        maxlength="2000"
        warnlength="200" />

      <p class="footage"><a href="javascript:;" @click="showProtocol">ThinkSNS+</a></p>

    </form>

    <!--  -->
    <choose-group-cate ref="chooseGroupCate" @change="onGroupCateChange"/>

    <!--  -->
    <group-protocol ref="protocol"/>

  </div>
</template>

<script>
import ChooseGroupCate from "./components/chooseGroupCate.vue";
import GroupProtocol from "./components/GroupProtocol.vue";
import { encodeGeoHash } from "@/util/geohash";

export default {
  name: "GroupCreate",
  components: {
    ChooseGroupCate,
    GroupProtocol
  },
  data() {
    return {
      loading: false,
      needPaid: false,
      category: {}, // 
      tags: [], // 
      location: {
        label: ""
      },
      avatar: null, // , Blob  File 

      privateMode: false, // 

      form: {
        name: "", // 
        summary: "", // 
        allow_feed: 0,
        mode: "public",
        money: "",
        notice: "" // 
      }
    };
  },
  computed: {
    disabled() {
      // 
      if (
        !this.avatar ||
        !this.form.name ||
        !this.tags.length ||
        !this.form.mode
      )
        return true;

      //   
      if (this.form.mode === "paid" && !this.form.money.match(/^\d{1,8}$/))
        return true;

      return false;
    },
    allowFeed: {
      get() {
        return !!this.form.allow_feed;
      },
      set(val) {
        this.form.allow_feed = val ? 1 : 0;
      }
    }
  },
  watch: {
    privateMode(val) {
      this.form.mode = !val ? "public" : this.needPaid ? "paid" : "private";
    },
    needPaid(val) {
      if (val) this.form.mode = "paid";
      else {
        this.form.mode = "private";
        this.form.money = null;
      }
    },
    "form.money"(val) {
      if (val) this.needPaid = true;
    }
  },
  methods: {
    showProtocol() {
      this.$refs.protocol.show();
    },
    switchCate() {
      this.$refs.chooseGroupCate.show();
    },
    onGroupCateChange(cate) {
      this.category = cate;
    },
    async handleOk() {
      if (this.loading) return;
      this.loading = true;

      //  FormData  ()
      const formData = new FormData();

      // 
      for (const key in this.form) {
        if (!this.form[key]) continue;
        formData.append(key, this.form[key]);
      }

      // 
      for (const tag of this.tags) {
        formData.append("tags[][id]", tag.id);
      }

      // 
      if (this.location.label) {
        const geoHash = encodeGeoHash(this.location.lat, this.location.lng);
        formData.append("location", this.location.label);
        formData.append("latitude", this.location.lat);
        formData.append("longitude", this.location.lng);
        formData.append("geo_hash", geoHash);
      }

      // 
      formData.append("avatar", this.avatar);

      // 
      const payload = { category: this.category.id, formData };
      const result = await this.$store.dispatch("group/createGroup", payload);
      this.loading = false;
      this.$Message.success(result);
      this.goBack();
    }
  }
};
</script>

